import React, { useState } from "react";
import { Flex, Space, Tabs } from "antd";
import CardCompetences from "./CardCompetences";
import logoReact from "../../Assets/img/React.svg";
import logoHTMLCSS from "../../Assets/img/CSS3 HTML5.png";
import logoJS from "../../Assets/img/logo-javascript.svg";
import logoKasa from "../../Assets/img/Kasa-logo.png";
import logoOMF from "../../Assets/img/ohmyfood.png";
import imgCom from "../../Assets/img/comIcon.png";
import imgAdaptation from "../../Assets/img/adaptation.png";
import imgFlex from "../../Assets/img/flexibility.svg";

import "./SectionCompetences.scss";
import CardProject from "../CardProject/CardProject.jsx";

const SectionCompetences = () => {
  const [tabKey, setTabKey] = useState("1");

  const onChange = (key) => {
    setTabKey(key);
  };

  const items = [
    {
      key: "1",
      label: "Comptences techniques",
      children: (
        <div>
          <Space direction="vertical" size={16}>
            <CardCompetences
              tabKey={tabKey}
              title="React"
              logo={logoReact}
              alt="Logo React"
              description="React est une bibliothque base sur le language Javascript permettant de fractionner le code en composant reutilisable et fournissant beaucoup d'outils pour amliorer le developpement. React est extremement populaire auprs de nombreux developpeurs."
              modalContent={
                <CardProject
                  title="Kasa"
                  source={logoKasa}
                  alt="Logo Kasa"
                  description="Site de location immobiliaire cr en utilisant React."
                  siteToVisit="https://lilhbt.github.io/Kasa/"
                />
              }
            />
            <CardCompetences
              tabKey={tabKey}
              title="JavaScript"
              logo={logoJS}
              alt="Logo Javascript"
              description="Javascript est un language de programmation permettant d'ajouter de l'intractivit  la page web."
            />

            <CardCompetences
              tabKey={tabKey}
              title="HTML5/CSS3"
              logo={logoHTMLCSS}
              alt="Logo HTML/CSS"
              description="Les languages HTML et CSS forment la base du developement web, 
              le premier permet de structurer la page et ensuite le second offre la possibilit de styliser le site."
              modalContent={
                <CardProject
                  title="Oh-My-Food"
                  source={logoOMF}
                  alt="Logo Oh-My-Food"
                  description="Projet de site de prise de commande, utilisant uniquement HTML5/CSS3 pour l'intgralite du code."
                  siteToVisit="https://lilhbt.github.io/Oh-my-food/"
                />
              }
            />
          </Space>
        </div>
      ),
    },
    {
      key: "2",
      label: "Comptences transverses",
      children: (
        <div>
          <Space direction="vertical" size={16}>
            <CardCompetences
              tabKey={tabKey}
              logo={imgCom}
              alt="Icon Communication"
              title="Ecoute et communication"
              description="Grce  mes expriences professionelle passe, j'ai pu acqurir une grande capacit de communication afin d'tre certain d'obtenir
               et de transmettre toutes les informations necssaire au bon droulemnt d'un projet."
            />
            <CardCompetences
              tabKey={tabKey}
              logo={imgAdaptation}
              alt="Icon Adaptation"
              title="Adaptation aux besoins"
              description="Ma capacit d'adaptation  vos besoins me permettra de mettre a profits toutes mes connaissances afin de les utiliser au mieux pour votre projet."
            />
            <CardCompetences
              tabKey={tabKey}
              logo={imgFlex}
              alt="Icon Flexibilit"
              title="Flexibilit"
              description="Je saurai mettre en place de nouvelle mthode et toujours rester au courant des nouveauts afin de vous proposer un projet au gout du jour."
            />
          </Space>
        </div>
      ),
    },
  ];
  return (
    <section>
      <div className="competences--wrapper">
        <div>
          <h3>Mes comptences</h3>
          <p>que je mettrai en oeuvre pour vos projets</p>
        </div>
        <Flex justify="center" gap={100}>
          <Tabs
            animated
            size="middle"
            centered
            defaultActiveKey="1"
            items={items}
            onChange={onChange}
            className="tabs"
          />
        </Flex>
      </div>
    </section>
  );
};

export default SectionCompetences;
